<template>
  <div>
    <v-tabs
        fixed-tabs
        background-color="green"
        dark
    >
      <v-tab v-on:click="getVova(1)">
        Котики
      </v-tab>
      <v-tab v-on:click="getVova(2)">
        Собачки
      </v-tab>
      <v-tab v-on:click="getVova(3)">
        Хомячки
      </v-tab>
      <v-tab v-on:click="getVova(4)">
        Попугайчики
      </v-tab>
    </v-tabs>
  </div>
</template>

<script>
import store from '@/Store/index.js';
import router from "@/router";

export default {
  methods:{
    getVova(int){
      if(int === 1)
      {
        store.commit("Page1")
        router.push("/")
      }
      if(int === 2)
      {
        store.commit("Page2")
        router.push("/Pages/VDogs")
      }
      if(int === 3)
      {

        store.commit("Page3")
        router.push("/Pages/VHamsters")
      }
      if(int === 4)
      {
        store.commit("Page4")
        router.push("/Pages/VParrots")
      }
    }
  }
}
</script>

